import './projects.css';
import React from 'react';
import Web from './web/web';
import SlideRoutes from 'react-slide-routes'
import {NavLink, Route, useLocation} from 'react-router-dom';
import Android from './android/android';
import Opensource from './opensource/openSource';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAndroid } from "@fortawesome/free-brands-svg-icons";
import { faGlobe } from '@fortawesome/free-solid-svg-icons';


// class component
const Projects = () => {



    const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

    const location = useLocation();
    // apply vriables here
      return (
      <div>
          <div>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
        open={open}
        autoHideDuration={4000}
        onClose={handleClose}
        message="Projects will be uploaded soon."
        action={
          <React.Fragment>
            <IconButton size="small" aria-label="close" color="inherit" onClick={handleClose}>
              <CloseIcon fontSize="small" />
            </IconButton>
          </React.Fragment>
        }
      />
    </div>
        <div className="projects-container">
            <div className="projects-logo">
             <h1>PROJECTS</h1>
            </div>
            <ul id="project-list">
                <li>
                    <NavLink to="/" exact activeClassName="active">Web <FontAwesomeIcon icon={faGlobe}/></NavLink>
                </li>
                <li><NavLink exact to="/android" onClick={handleClick}>Android <FontAwesomeIcon icon={faAndroid}/></NavLink></li>
                <li><NavLink exact to="/opensource">Open Source</NavLink></li>
            </ul>
            <div className="slide-box">
                <SlideRoutes location={location}>
                    <Route exact path="/" component={Web} />
                    <Route exact path="/android" component={Android}/>
                    <Route exact path="/opensource" component={Opensource}/>
                    <Route exact path="/:id">
                        <p>Error 404</p>
                    </Route>
                </SlideRoutes>
            </div>
        </div>
    </div>
);
}

export default Projects;
